import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { TenantProfile } from '@core/models';
import { WindowService } from '@core/services/window.service';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { finalize } from 'rxjs';

@Component({
    selector: 'findqo-include-rental-resume-dialog',
    templateUrl: './include-rental-resume-dialog.component.html',
    styleUrl: './include-rental-resume-dialog.component.scss',
    standalone: false
})
export class IncludeRentalResumeDialogComponent {
	constructor(
		private readonly dialogRef: MatDialogRef<IncludeRentalResumeDialogComponent>,
		@Inject(MAT_DIALOG_DATA) public data: { tenantProfile: TenantProfile; sharedFacade: SharedAdsFacade; windowService: WindowService }
	) {}
	appendTenantinPdf = true;
	fetchingPdf = false;
	sharedAdsFacade = this.data.sharedFacade;

	handlePdfPreview(): void {
		this.fetchingPdf = true;
		this.sharedAdsFacade
			.getPdfLocalUrl(this.data.tenantProfile.tenantResumeUrl)
			.pipe(finalize(() => (this.fetchingPdf = false)))
			.subscribe((res) => {
				const LINK = URL.createObjectURL(res);
				this.data.windowService.reference.open(LINK);
			});
	}

	closeDialog(): void {
		this.dialogRef.close(this.appendTenantinPdf);
	}
}
