import { Injectable } from '@angular/core';
import { AuthService } from '@core/data-access/services/auth.service';
import { AmplitudeService } from './amplitude.service';
import { AmplitudeEvent, StorageType } from '@core/enums';
import { Ad, ClientUser, SendInquiry } from '@core/models';
import { StorageService } from './storage.service';
import { ENQUIRY_LIMIT } from '@core/constants';

@Injectable({ providedIn: 'root' })
export class EnquiryService {
	constructor(
		private authService: AuthService,
		private amplitudeService: AmplitudeService,
		private storageService: StorageService
	) {}

	onOpenSendEnquiryDialog(amplitudeEvent: AmplitudeEvent, openDialogCallback: () => void): void {
		const user = this.authService.user();
		const isLoggedIn = !!user;

		this.amplitudeService.handleSendEnquiryEvents(amplitudeEvent, {
			user_logged_in: isLoggedIn,
			...(isLoggedIn && { user_email: user.email, user_id: user.id }),
		});

		/**
		 * Send enquiry limitation
		 *
		 * Logout users has a limit of 100 on sending enquiries.
		 * Login users doesn't have limit on sending enquiries.
		 */
		const count = this.storageService.getItem(StorageType.ENQUIRY_SEND_ATTEMPT_COUNT);
		const shouldOpenAuthDialog = !isLoggedIn && +count >= ENQUIRY_LIMIT;
		if (shouldOpenAuthDialog) {
			// Open auth dialog for non-logged-in users after exceeding attempt limit
			// SsoPostLogin isn't handled here since it's unlikely a user will exceed the 100 enquiry limit, so adding extra logic isn't justified.
			this.authService.openAuthDialog({ onLogin: openDialogCallback });
		} else {
			openDialogCallback();
		}
	}

	handleAmplitudeEvents(
		event: AmplitudeEvent,
		user: ClientUser,
		ad: Ad,
		additionalInfo?: { error?: string; errorId?: string; enquiryId?: string; enquiryFormValues?: SendInquiry }
	): void {
		this.amplitudeService.handleSendEnquiryEvents(event, {
			...(user && { user_email: user.email, user_id: user.id }),
			property_name: ad.title,
			property_id: ad.id,
			property_county: ad.county,
			...(additionalInfo?.errorId && { errorId: additionalInfo.errorId }),
			...(additionalInfo?.error && { error: additionalInfo.error }),
			...(additionalInfo?.enquiryId && { enquiry_id: additionalInfo.enquiryId }),
		});

		if (event === AmplitudeEvent.SUBMIT_ENQUIRY_FORM_SUCCESS) {
			this.incrementEnquiryAttemptCount();

			const isLoggedIn = !!this.authService.user();
			if (!isLoggedIn) {
				this.storageService.setItemSession(StorageType.ENQUIRY_FORM_VALUES, JSON.stringify(additionalInfo?.enquiryFormValues));
			}
		}
	}

	private incrementEnquiryAttemptCount(): void {
		const count = this.storageService.getItem(StorageType.ENQUIRY_SEND_ATTEMPT_COUNT);
		this.storageService.setItem(StorageType.ENQUIRY_SEND_ATTEMPT_COUNT, count ? (+count + 1).toString() : '1');
	}
}
