import { Platform } from '@angular/cdk/platform';
import { Component, Inject, OnDestroy, OnInit, PLATFORM_ID } from '@angular/core';
import { AbstractControl, FormArray, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { DateAdapter, MAT_DATE_LOCALE } from '@angular/material/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatRadioButton } from '@angular/material/radio';
import { Router } from '@angular/router';
import { DatePickerAdapter } from '@core/adapters';

import { HttpErrorResponse } from '@angular/common/http';
import { ENQUIRY_LIMIT } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { AisleType, AmplitudeEvent, MenuKeys, SectionType, SellerType, StorageType, UpdateAction } from '@core/enums';
import { ClientUser, CommonResponse, CreateTenantProfileResponse, Inquiry, SendEnquiryDialogData, SendInquiry, TenantProfile } from '@core/models';
import { EnquiryService } from '@core/services/enquiry.service';
import { IconService } from '@core/services/icon.service';
import { RouteDataType, RouteService } from '@core/services/route.service';
import { SnackService } from '@core/services/snack.service';
import { StorageService } from '@core/services/storage.service';
import { WindowService } from '@core/services/window.service';
import { AppEmailValidator } from '@core/validators';
import { environment } from '@environments/environment';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { finalize } from 'rxjs';
import { IncludeRentalResumeDialogComponent } from './include-rental-resume-dialog/include-rental-resume-dialog.component';

// TODO: Move PaymentMode enum on separate file
export enum PaymentMode {
	CASH = 'Cash',
	MORTGAGE = 'Mortgage',
}

enum LoadingState {
	CONNECT_ACCOUNT = 0,
	GET_PDF = 1,
	CREATE_TENANTIN_PROFILE = 2,
	COMPONENT = 3,
	SEND_OTP = 4,
	SUBMIT_FORM = 5,
}

// TODO: Refactor and optimize the codes here
@Component({
    selector: 'findqo-send-inquiry-dialog',
    templateUrl: './send-inquiry-dialog.component.html',
    styleUrls: ['./send-inquiry-dialog.component.scss'],
    providers: [
        {
            provide: DateAdapter,
            useClass: DatePickerAdapter,
            deps: [MAT_DATE_LOCALE, Platform],
        },
    ],
    standalone: false
})
export class SendInquiryDialogComponent implements OnInit, OnDestroy {
	constructor(
		public readonly icon: IconService,
		@Inject(MAT_DIALOG_DATA) public dialogData: SendEnquiryDialogData,
		private readonly dialogRef: MatDialogRef<SendInquiryDialogComponent>,
		private readonly fb: FormBuilder,
		private readonly snack: SnackService,
		private readonly auth: AuthService,
		private readonly window: WindowService,
		private readonly router: Router,
		private readonly routeService: RouteService,
		private readonly dialog: MatDialog,
		private readonly profileFacde: MyProfileFacade,
		private readonly enquiryService: EnquiryService,
		private readonly storageService: StorageService,
	) {
		this.user = this.auth.user();
		this.isPublicPage = this.routeService.getRouteData()?.[RouteDataType.IS_PUBLIC_PAGE];
	}

	defaultUserImg: string = environment.defaultAvatarImg;
	minDate = +this.dialogData.ad.availableFrom * 1000 > Date.now() ? new Date(+this.dialogData.ad.availableFrom * 1000) : new Date();
	maxDate: Date;
	user: ClientUser;
	ad = this.dialogData.ad;
	appendTenantinPdf = new FormControl(false);
	resendOtpCooldownTime = 45;
	adultsCount = 1;
	timer: NodeJS.Timeout;
	tenantInformation: TenantProfile;
	detectedTenantinUrl = '';
	detectedTenantinEmail = undefined;
	isLimitReached = {
		status: 0,
		translation: 'n3707',
	};

	loadingState = {
		[LoadingState.CONNECT_ACCOUNT]: false,
		[LoadingState.GET_PDF]: false,
		[LoadingState.COMPONENT]: false,
		[LoadingState.CREATE_TENANTIN_PROFILE]: false,
		[LoadingState.SEND_OTP]: false,
		[LoadingState.SUBMIT_FORM]: false,
	};

	toggleTenantinCreateAccount = false;
	hasTenantinAccount = false;
	showResendOtp = false;
	isOtpSent = false;
	isTenantInProfile = true;
	isRentCommercial = false;
	isPublicPage = false;
	showSuccessMessage = false;
	isResubmitEnquiry = false;

	readonly buttonType = ButtonType;
	readonly sharedAdsFacade = this.dialogData.sharedAdsFacade;
	readonly sellerType = SellerType;
	readonly sectionType = SectionType;
	readonly paymentMode = PaymentMode;
	readonly loadingStateEnum = LoadingState;
	readonly enquiryLimit = ENQUIRY_LIMIT;

	//TODO: get the prefilled data of client from api later
	tenantForm: FormGroup;

	connectEmailForm: FormGroup = this.fb.group({
		email: [undefined, [Validators.required, AppEmailValidator.validateEmailPattern()]],
		pin: [undefined, [Validators.required, Validators.minLength(6), Validators.pattern('[0-9]+')]],
	});

	get fc(): { [p: string]: AbstractControl } {
		return this.tenantForm.controls;
	}

	updateRadioBtnChild(event: MatRadioButton, fromRadioButton: boolean): void {
		if (!fromRadioButton) {
			event.checked = !event.checked;
		}

		if (event.value || !fromRadioButton) {
			this.tenantForm.get('hasChild').setValue(false);
		}

		if (event.value || fromRadioButton) {
			this.tenantForm.get('hasChild').setValue(true);
		}
	}

	updateRadioBtnPet(event: MatRadioButton, fromRadioButton: boolean): void {
		if (!fromRadioButton) {
			event.checked = !event.checked;
		}

		if (event.value || !fromRadioButton) {
			this.tenantForm.get('hasPets').setValue(false);
		}

		if (event.value || fromRadioButton) {
			this.tenantForm.get('hasPets').setValue(true);
		}
	}

	addTenantinUrl(checked: boolean): void {
		if (checked) {
			this.connectEmailForm.patchValue({
				email: this.detectedTenantinUrl,
			});
		} else {
			this.connectEmailForm.patchValue({
				email: '',
			});
		}
	}

	changeAccount(): void {
		this.toggleTenantinCreateAccount = true;
		this.detectedTenantinUrl = '';
		this.connectEmailForm.controls['email'].enable();
		this.isOtpSent = false;
	}

	connectAccount(loadingState: LoadingState): void {
		if (this.connectEmailForm.controls['email'].invalid) {
			return;
		}

		if (this.isOtpSent) {
			if (this.connectEmailForm.valid) {
				this.verifyOtp();
			}
		} else {
			this.sendOtp(loadingState);
		}
	}

	verifyOtp(): void {
		this.loadingState[LoadingState.CONNECT_ACCOUNT] = true;

		const FORM_DATA = {
			email: this.connectEmailForm.controls['email'].value,
			pin: this.connectEmailForm.controls['pin'].value,
		};

		this.sharedAdsFacade
			.verifyTenantOtp(FORM_DATA)
			.pipe(finalize(() => (this.loadingState[LoadingState.CONNECT_ACCOUNT] = false)))
			.subscribe(
				(res) => {
					this.detectedTenantinUrl = res.data?.['profileUrl'];
					this.detectedTenantinEmail = res.data?.['email'];
					this.tenantInformation = res.data;
					this.connectEmailForm.reset();
					this.isLimitReached.status = 0;
					this.appendTenantinPdf.patchValue(true);
					this.onToggleTenantinCreateAccount();
					this.checkForTenantinAccount(this.detectedTenantinUrl);

					this.snack.open({
						data: { translationKey: 'n3743', type: 'success' },
						panelClass: ['snackbar-success'],
					});
				},
				() => {
					this.connectEmailForm.get('pin').setErrors({ incorrectPin: true });
				},
			);
	}

	sendOtp(loadingState: LoadingState): void {
		this.loadingState[loadingState] = true;

		this.sharedAdsFacade
			.sendTenantOtp({ email: this.connectEmailForm.controls['email'].value })
			.pipe(finalize(() => (this.loadingState[loadingState] = false)))
			.subscribe(
				() => {
					this.startOtpCooldownTimer();
					this.isOtpSent = true;
					this.connectEmailForm.controls['email'].disable();
				},
				(err) => {
					this.connectEmailForm.controls['email'].enable();
					this.isOtpSent = false;

					if (err?.error?.error?.id === 'accountNotDetected') {
						this.connectEmailForm.get('email').setErrors({ accountNotDetected: true });
						return;
					}

					this.snack.open({
						data: { translationKey: 'n608', type: 'error' },
						panelClass: ['snackbar-error'],
					});
				},
			);
	}

	onToggleTenantinCreateAccount(): void {
		this.toggleTenantinCreateAccount = !this.toggleTenantinCreateAccount;
		this.connectEmailForm.reset();
		clearInterval(this.timer);
	}

	createTenantProfile(): void {
		this.loadingState[LoadingState.CREATE_TENANTIN_PROFILE] = true;

		this.sharedAdsFacade
			.createTenantProfile()
			.pipe(finalize(() => (this.loadingState[LoadingState.CREATE_TENANTIN_PROFILE] = false)))
			.subscribe(
				(res: CommonResponse<CreateTenantProfileResponse>) => {
					const OPENED_TAB = this.window.reference.open(`${environment.tenantInCreateProfileUrl}?findqoAuthToken=${res.data.accessToken}`, '_blank');

					// If popup window is disabled from the settings
					if (!OPENED_TAB || OPENED_TAB.closed || typeof OPENED_TAB.closed === 'undefined') {
						this.snack.open({
							data: { translationKey: 'n3965', type: 'error' },
							panelClass: ['snackbar-error'],
						});
					}
				},
				() => {
					this.snack.open({
						data: { translationKey: 'n3736', type: 'error' },
						panelClass: ['snackbar-error'],
					});
				},
			);
	}

	handlePdfPreview(): void {
		this.loadingState[LoadingState.GET_PDF] = true;
		this.sharedAdsFacade
			.getPdfLocalUrl(this.tenantInformation.tenantResumeUrl)
			.pipe(finalize(() => (this.loadingState[LoadingState.GET_PDF] = false)))
			.subscribe((res) => {
				const LINK = URL.createObjectURL(res);
				this.window.reference.open(LINK);
			});
	}

	// TODO: Refactor this function. This is not good as it's more than 100 lines of code for a function/method!
	onSubmit(isSubmittedFromDialog = false): void {
		this.enquiryService.handleAmplitudeEvents(AmplitudeEvent.ENQUIRY_FORM_SUBMITTED, this.user, this.ad);

		if (!this.tenantForm.valid) {
			this.enquiryService.handleAmplitudeEvents(
				AmplitudeEvent.SUBMIT_ENQUIRY_FORM_FAILED,
				{
					...this.user,
					name: this.fc['name']?.value,
					email: this.fc['email']?.value,
				},
				this.ad,
				{ error: 'Form incomplete or invalid! Please check all the fields.' },
			);
		}

		//to remove the error shown in case of no value
		if ((!this.isRentCommercial && !this.connectEmailForm.get('email')?.value) || this.detectedTenantinUrl) {
			this.connectEmailForm.get('email').setErrors(null);
		}

		if (this.tenantForm.get('date')?.pristine && this.tenantForm.get('date')?.errors) {
			this.tenantForm.get('date').setErrors(null);
		}

		if (
			!this.tenantForm.valid &&
			(this.tenantForm?.get('tenantinUrl')?.valid ||
				Object.keys(this.tenantForm.controls).find((key) =>
					this.tenantForm?.controls[key]?.invalid && key === 'tenantinUrl' ? this.tenantForm?.controls[key]?.value : true,
				))
		) {
			this.snack.open({
				data: { translationKey: 'n3214', type: 'error' },
				panelClass: ['snackbar-error'],
			});
		} else {
			if (!this.appendTenantinPdf.value && !this.isRentCommercial && this.detectedTenantinUrl && !isSubmittedFromDialog) {
				this.openIncludeResumeDialog();
				return;
			}

			this.loadingState[LoadingState.SUBMIT_FORM] = true;

			const FORM_DATA = this.tenantForm.value;
			const MOVE_IN_DATE = FORM_DATA?.date ? FORM_DATA.date : null;

			let sendInquiryValues: SendInquiry;
			if (this.ad.section.reference === SectionType.PROPERTIES_FOR_SALE) {
				sendInquiryValues = {
					phone: FORM_DATA.phone,
					message: FORM_DATA.message,
					paymentMode: FORM_DATA.paymentMode,
				};
			} else if (this.isRentCommercial) {
				sendInquiryValues = {
					phone: FORM_DATA.phone,
					message: FORM_DATA.message,
				};
			} else {
				sendInquiryValues = {
					phone: FORM_DATA.phone,
					adultsCount: FORM_DATA.adults.toString(),
					withTenantResume: this.appendTenantinPdf.value ? '1' : '0',

					pet: {
						hasPet: FORM_DATA.hasPets ? '1' : '0',
					},

					message: FORM_DATA.message,
				};
			}

			if (MOVE_IN_DATE) {
				const unixTimestamp = Math.floor(MOVE_IN_DATE / 1000); // Convert to seconds
				const date = new Date(unixTimestamp * 1000); // Create a Date object from the Unix timestamp
				const formattedDate = date.toISOString().split('T')[0]; // Extract the date in "YYYY-MM-DD" format

				sendInquiryValues = {
					...sendInquiryValues,
					moveDate: formattedDate,
					moveInDate: unixTimestamp.toString(),
				};
			}

			//check if its not resubmit
			if (!this.isResubmitEnquiry && !this.dialogData?.isReSubmitEnquiry) {
				this.isResubmitEnquiry = true;
				const enquiryFormValues = {
					...sendInquiryValues,
					name: this.fc['name'].value,
					email: this.fc['email'].value,
				};

				//send enquiry
				this.sharedAdsFacade
					.sendInquiry(this.dialogData.ad.id, enquiryFormValues)
					.then((inquiryData: CommonResponse<Inquiry>) => {
						this.enquiryService.handleAmplitudeEvents(
							AmplitudeEvent.SUBMIT_ENQUIRY_FORM_SUCCESS,
							{
								...this.user,
								name: this.fc['name'].value,
								email: this.fc['email'].value,
							},
							this.ad,
							{
								enquiryId: inquiryData.data.id,
								enquiryFormValues,
							},
						);

						//TODO: add this in new sharedEnquriesfacade and get data from state
						//add enquiry data to dialogdata if its not from my enquiry page
						this.dialogData = { ...this.dialogData, inquiryData: inquiryData?.data };

						const isLoggedIn = !!this.user;
						if (isLoggedIn) {
							this.profileFacde.updateProfileValues(MenuKeys.ENQUIRY_COUNT, UpdateAction.ADD);
						}

						this.dialogRef.backdropClick().subscribe(() => this.closeForm());

						this.showSuccessMessage = true;
						this.loadingState[LoadingState.SUBMIT_FORM] = false;
					})
					.catch((error: HttpErrorResponse) => {
						const errorMessage = error?.error?.error?.errors?.[0]?.messages?.[0] || error?.message || 'Unknown error occurred';
						const errorId = error?.error?.error?.id;

						this.enquiryService.handleAmplitudeEvents(
							AmplitudeEvent.SUBMIT_ENQUIRY_FORM_FAILED,
							{
								...this.user,
								name: this.fc['name'].value,
								email: this.fc['email'].value,
							},
							this.ad,
							{ error: errorMessage.toString(), errorId },
						);

						this.isResubmitEnquiry = false;
						this.loadingState[LoadingState.SUBMIT_FORM] = false;

						this.snack.open({
							data: { translationKey: 'n608', type: 'error' },
							panelClass: ['snackbar-error'],
						});

						if (errorId) {
							this.fc['email']?.setErrors({ [errorId]: true });
						}
					});
			}
			//if resubmit enquiry
			else if (this.dialogData?.inquiryData) {
				const RESUBMIT_ENQUIRY_DATA = {
					...sendInquiryValues,
					// ...(this.isPublicPage && {
					name: this.fc['name'].value,
					email: this.fc['email'].value,
					// }),
				};

				this.sharedAdsFacade.handleEnquiryReSubmit(RESUBMIT_ENQUIRY_DATA, this.dialogData, !this.isRentCommercial, this.dialogRef);
				this.loadingState[LoadingState.SUBMIT_FORM] = false;
			}
		}
	}

	openIncludeResumeDialog(): void {
		this.dialog
			.open(IncludeRentalResumeDialogComponent, {
				width: '95%',
				maxWidth: '500px',
				data: {
					tenantProfile: { ...this.tenantInformation, isIncludeResumeRequired: Boolean(this.dialogData?.isIncludeResumeRequired) },
					sharedFacade: this.sharedAdsFacade,
					windowService: this.window,
				},
				panelClass: ['full-screen'],
				backdropClass: 'tenantin-backdrop',
			})
			.afterClosed()
			.subscribe((res) => {
				//Submit the form only if user has pressed the submit button on dialog
				if (typeof res === 'boolean') {
					this.appendTenantinPdf.setValue(res);
					this.onSubmit(true);
				}
			});
	}

	showWhyTenantin(): void {
		this.window.reference.open(environment.tenantInWhyUrl, '_blank', 'noopener');
	}

	decreaseAdultCount(): void {
		if (this.adultsCount > 0) {
			--this.adultsCount;
		}
	}

	increaseAdultCount(): void {
		++this.adultsCount;
	}

	closeForm(): void {
		if (this.showSuccessMessage === true) {
			this.dialogRef.close(this.dialogData);
		} else {
			this.dialogRef.close();
		}
	}

	private startOtpCooldownTimer() {
		this.showResendOtp = false;
		this.resendOtpCooldownTime = 45;

		if (this.timer) {
			clearInterval(this.timer);
		}

		this.timer = setInterval(() => {
			this.resendOtpCooldownTime = this.resendOtpCooldownTime - 1;

			if (this.resendOtpCooldownTime === 0) {
				this.showResendOtp = true;
				this.resendOtpCooldownTime = undefined;

				clearInterval(this.timer);
			}
		}, 1000);
	}

	private setMaximumDate() {
		this.maxDate = new Date();
		this.maxDate.setFullYear(new Date().getFullYear() + 2);
	}

	private updateInquiryData(): void {
		const AVAILABLE_MIN_DATE = +this.dialogData.ad.availableFrom * 1000 > Date.now() ? this.minDate.getDate() : this.minDate.getDate() + 1;
		this.minDate.setDate(AVAILABLE_MIN_DATE);

		//if from my enquiries page
		if (this.dialogData?.isReSubmitEnquiry && this.dialogData?.inquiryData) {
			this.prefillEnquiryForm(this.dialogData.inquiryData);
			this.loadingState[LoadingState.COMPONENT] = false;
		} else {
			//TODO: load this data in parent component and get in new sharedEnquriesFacade
			this.sharedAdsFacade.loadInquiry(this.dialogData?.ad?.id).then((response) => {
				this.prefillEnquiryForm(response?.data[0]);
				this.isLimitReached = {
					status: !+response?.meta?.hasTenantAccount && +response?.meta?.hasLimitReached,
					translation: 'n3707',
				};

				this.dialogData = {
					...this.dialogData,
					isIncludeResumeRequired: +(response?.meta?.hasLimitReached ?? '0') && +(response?.meta?.hasTenantAccount ?? '0'),
				};

				// TODO: Remove this code when API will update the response type
				if (!Array.isArray(response.data)) {
					this.isLimitReached = {
						status: 0,
						translation: 'n3707',
					};
				}

				this.loadingState[LoadingState.COMPONENT] = false;
			});
		}
	}

	private checkForTenantinAccount(tenantinUrl = ''): void {
		const HAS_TENANTIN_URL = Boolean(tenantinUrl);

		this.appendTenantinPdf.setValue(HAS_TENANTIN_URL);
		this.detectedTenantinUrl = HAS_TENANTIN_URL ? tenantinUrl : '';
		this.toggleTenantinCreateAccount = !HAS_TENANTIN_URL;
		this.hasTenantinAccount = HAS_TENANTIN_URL;
	}

	private prefillEnquiryForm(data: Inquiry) {
		this.checkForTenantinAccount(data?.tenantin?.tenantResumeUrl);
		this.tenantInformation = data?.tenantin;
		this.detectedTenantinEmail = data?.tenantin?.email;

		this.adultsCount = +data?.adultsCount || 1;

		if (this.ad.section.reference === SectionType.PROPERTIES_FOR_SALE) {
			const UPDATED_VALUES = {
				name: this.user?.name || data?.name,
				email: this.user?.email || data?.email,
				phone: data?.phone,
			};

			this.tenantForm.patchValue(UPDATED_VALUES);
		} else if (!this.isRentCommercial) {
			const UPDATED_VALUES = {
				name: this.user?.name || data?.name,
				email: this.user?.email || data?.email,
				phone: data?.phone,
				adults: data?.adultsCount || 1,
			};

			this.tenantForm.patchValue(UPDATED_VALUES);
		} else {
			const UPDATED_VALUES = {
				name: this.user?.name || data?.name,
				email: this.user?.email || data?.email,
				phone: data?.phone,
			};

			this.tenantForm.patchValue(UPDATED_VALUES);
		}
	}

	private removeFormControls(): void {
		this.tenantForm.removeControl('adults');
		this.tenantForm.removeControl('hasPets');

		if (this.ad.section.reference === SectionType.PROPERTIES_FOR_SALE) {
			// "Move in date" is not needed for Property For Sale
			this.tenantForm.removeControl('date');
		} else {
			// "Payment mode" is only for Property For Sale
			this.tenantForm.removeControl('paymentMode');
		}
	}

	private checkIsRentCommercial(): void {
		if (
			this.router.url.includes(AisleType.RENT_COMMERCIAL) ||
			this.dialogData.ad.aisle.reference === AisleType.RENT_COMMERCIAL ||
			this.ad.section.reference === SectionType.PROPERTIES_FOR_SALE
		) {
			this.removeFormControls();
			this.isRentCommercial = true;
		}
	}

	private initDeclarations(): void {
		const isLoggedIn = !!this.user;

		// Set initial loading state
		this.loadingState[LoadingState.COMPONENT] = isLoggedIn;

		if (this.isPublicPage) {
			// Enable required fields for public pages
			this.fc['name'].enable();
			this.fc['email'].enable();

			// Adjust the loading state and minimum date
			this.loadingState[LoadingState.COMPONENT] = false;
		} else if (isLoggedIn) {
			this.updateInquiryData(); // Populate the form for an existing user
		}

		// Adjust minimum date and prefill form if applicable
		if (!isLoggedIn || this.isPublicPage) {
			this.minDate.setDate(this.minDate.getDate() + 1);
			const enquiryFormValues = this.storageService.getItemSession(StorageType.ENQUIRY_FORM_VALUES);
			if (enquiryFormValues) {
				this.prefillEnquiryForm(JSON.parse(enquiryFormValues));
			}
		}

		// Configure date validation and form settings
		this.setMaximumDate();
		this.checkIsRentCommercial();
	}

	private createTenantForm(): void {
		const isLoggedIn = !!this.user;

		this.tenantForm = this.fb.group({
			name: [{ value: undefined, disabled: isLoggedIn }, Validators.required],
			email: [{ value: undefined, disabled: isLoggedIn }, [Validators.required, Validators.email]],
			phone: [undefined, [Validators.required, Validators.pattern('^[0-9]*$')]],
			adults: [undefined, Validators.required],
			date: [undefined],
			message: [`Hi,\n\nI am interested in your property on FindQo.ie. Link: ${this.ad.adlink}\n\nAd ID: ${this.ad.id}`, [Validators.required]],
			hasPets: [undefined],
			paymentMode: [undefined],
		});
	}

	ngOnInit(): void {
		this.createTenantForm();
		this.enquiryService.handleAmplitudeEvents(AmplitudeEvent.ENQUIRY_FORM_OPENED, this.user, this.ad);
		this.initDeclarations();
	}

	ngOnDestroy(): void {
		clearInterval(this.timer);
	}
}
